.branches {
  padding-top: 2rem;
  background-color: var(--light-grey-color);
  text-align: center;
  width: 100%;
  position: relative;

  .watermark {
    width: 14rem;
    position: absolute;
    overflow: hidden;
    top: -10%;
    left: 85%;
    opacity: 0.9;
    clip-path: polygon(0 13%, 100% 20%, 100% 100%, 75% 100%, 0% 100%);
    display: none;
  }

  h3 {
    color: var(--black-color);
    text-transform: uppercase;
    font-size: 18px;
  }
  span {
    text-align: center;
    color: var(--secondary-color);
  }
  .branchContainer {
    width: 80%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin: auto;
  }
  // .branch:nth-of-type(1){background: url("/images/Layer 23.png") center ;}
  // .branch:nth-of-type(2){background: url("/images/Layer 24.png") center ;}
  // .branch:nth-of-type(3){background: url("/images/Layer 25.png") center no-repeat;}
  // .branch:nth-of-type(4){background: url("/images/Layer 26.png") center ;}
  .branch {
    z-index: 2;
    margin: 2rem;
    width: 25%;
    height: 10rem;
    display: flex;
    align-items: center;
    justify-content: center;
    &:hover {
      transform: scale(1.1);
      transition: transform 4s cubic-bezier(0.25, 0.45, 0.45, 0.95);
    }
    h4 {
      color: var(--white-color);
      font-size: 1.5rem;
      font-family: "Cairo", sans-serif;
    }
  }
}

@media screen and (max-width: 800px) {
  .branches {
    .branchContainer {
      flex-wrap: wrap;
      flex-direction: column;
      .branch {
        width: 80%;
      }
    }
  }
}
