.banner {
  background-color: var(--primary-color);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 6rem;
  height: 3rem;
  width: 100vw;

  .creditcards {
    display: flex;
    justify-content: space-between;
    align-items: center;

    .currency {
      font-size: 15px;
      color: var(--white-color);
      margin-left: 1rem;
      i {
        font-size: 10px;
        margin-left: 3px;
        cursor: pointer;
      }
    }
  }

  .storeInfo {
    display: flex;
    div {
      padding: 1rem;
      color: var(--white-color);
      font-size: 15px;
      span {
        padding: 0.5rem;
      }
      &:hover {
        color: var(--secondary-color);
      }
    }
  }
}

@media screen and (max-width: 800px) {
  .banner {
    padding: 0;
    justify-content: space-between;
    font-size: 10px;
    .storeInfo {
      div {
        width: 6rem;
        padding: 0;

        span {
          font-size: 10px;
        }
      }
    }
  }
  .currency {
    font-size: 10px;
    margin-left: 0.2rem;
    .i {
      font-size: 5px;
    }
  }
}

@media screen and (max-width: 500px) {
  .banner {
    flex-direction: column;
    height: 5rem;
    justify-content: center;

    .creditcards {
      margin-bottom: 1rem;
    }
  }
}
